
#root{
  width: 100%;
  height: 100%;
}
.Content{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
}

.NavBar{
  width: 100%;
  height: 10vh;
  display: flex;
  align-items: center;
  background-color: rgb(104, 104, 104);
}

.MainContent{
  display: flex;
  flex-direction: column;
  height: 130vh;
  width: 100%;
}

#contactDiv{
  height: 10vh;
  width: 100%;
  background-color: rgb(104, 104, 104);
}
.Row{
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: row;
  
}
.Row div{
  height: 100%;
  width: 100%;
}

.Header{
  position: relative;
}
.TextContent{
  width:70%;
  height:90%;
  font-size: 16px;
  color: transparent;
  display: flex;
  justify-content: center;
  flex-direction: row;
  align-items: center;
  text-align: center;
}
.ContentWrapper{
  position: absolute;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.NavEl{
  height: 100%;
  align-items: center;
  display: flex;
  flex-direction: row;
  flex:1;
}

.NameEl{
  height: 100%;
  flex:1;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
}
.NameWrapper{
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  padding-right: 7%;
}
.NameWrapper > p{
  display: flex;
  justify-content: flex-end;
  align-items: center;
  color: white;
}

.NavElWrapper > a{
  display: flex;
  color: white;
  height: 19px;
  text-decoration: none;
}
.NavElWrapper > p{
  display: flex;
  justify-content: center;
  color: white;
  height: 19px;
  text-decoration: none;
}


.NavElWrapper{
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  width: fit-content;
  padding-left: 3%;
}

.NavElWrapper p:hover{
  border-bottom: 1px solid rgb(255, 254, 254);
  cursor: pointer;
}
.NavElWrapper a:hover{
  border-bottom: 1px solid rgb(255, 254, 254);
  cursor: pointer;
}

.ContactWrapper{
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 100%;
}
.ContactWrapper > a{
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  padding-left: 3%;
  color: white;
  text-decoration: none;
}
.ContactWrapper > p{
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  width: fit-content;
  color: white;
  padding-left: 3%;
  
}
.AboutMeComp{
  height: 80vh;
  width:100%;
  display:flex;
}
.AboutMeWrapper > p{
  height: fit-content;
  width: 80%;
  color: black;
}
.AboutMeWrapper{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.Picture{
  padding-left: 3%;
  height: 60vh;
  width: 60vh;
  border-radius: 100%;
}
.PictureWrapper{
  display:flex;
  justify-content: center;
  align-items: center;
}
.ContentWrapper > a {
  text-decoration: none;
}

p{
  margin: 0;
}

@media screen and (max-device-width: 875px){
  .Row{
    flex-direction: column;
  }
  .Content{
    display: flex;
    flex-direction: column;
  }
  .AboutMeComp{
    flex-direction: column;
    justify-content: space-evenly;
    height: 1024px;
  }
  .NavBar{
    height: calc(1024px *0.1);
  }
  #contactDiv{
    height: calc(1024px *0.1);
  }
 
 .MainContent{
    height: calc(1024px*1.5);
 }
 .Picture{
  padding: 0;
  margin: 0;
  height: calc(1024px*0.4);
  width:  calc(1024px*0.4);
}


}
@media screen and (max-width: 550px){
  .AboutMeWrapper p {
    text-align: center;
    font-size: 20px;
  }
  .ContactWrapper p{
    font-size: 10px;
  }
  .ContactWrapper a{
    font-size: 10px;
  }
  .NavBar p {
    font-size: 12px;
  }
  .NavBar a {
    font-size: 12px;
  }

}

@media screen and (max-device-width: 390px) {
  .Content{
    height: 1400px;
  }
  .NavBar{
    height: 5%;
  }
  #contactDiv{
    height: 5%;
  }
 
 .MainContent{
    height: 90%;
 }
 
  .Picture{
    padding: 0;
    margin: 0;
    height: 350px;
    width:  350px;
  }
  
  .AboutMeWrapper p {
    text-align: center;
    font-size: 20px;
  }
  .ContactWrapper p{
    font-size: 10px;
  }
  .ContactWrapper a{
    font-size: 10px;
  }
  .NavBar p {
    font-size: 12px;
  }
  .NavBar a {
    font-size: 12px;
  }
}

